// components/post/post-card.tsx
"use client";

import type React from "react";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import Link from "next/link";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Button } from "@/components/ui/button";
import { Heart, MessageCircle, LogIn, MoreVertical } from "lucide-react";
import { createClient } from "@/lib/supabase/client";
import { formatDistanceToNow } from "date-fns";
import { FollowButton } from "@/components/follow/follow-button";

interface Post {
  id: string;
  content: string;
  created_at: string;
  user_id: string;
  username: string;
  display_name: string;
  avatar_url: string | null;
  likes_count: number;
  comments_count: number;
  user_has_liked: boolean;
}

interface PostCardProps {
  post: Post;
  showFollowButton?: boolean;
  currentUserId?: string;
  isCurrentUserFollowing?: boolean;
  onFollowChange?: (userId: string, isFollowing: boolean) => void;
}

export function PostCard({ 
  post, 
  showFollowButton = false,
  currentUserId,
  isCurrentUserFollowing = false,
  onFollowChange
}: PostCardProps): React.JSX.Element {
  const router = useRouter();
  const [liked, setLiked] = useState<boolean>(post.user_has_liked);
  const [likesCount, setLikesCount] = useState<number>(post.likes_count);
  const [loading, setLoading] = useState<boolean>(false);
  const [isAuthenticated, setIsAuthenticated] = useState<boolean>(false);
  const [authUserId, setAuthUserId] = useState<string | null>(null);

  // Provjera da li je korisnik logovan
  useEffect(() => {
    const checkAuth = async (): Promise<void> => {
      const supabase = createClient();
      const { data: { user } } = await supabase.auth.getUser();
      setIsAuthenticated(!!user);
      setAuthUserId(user?.id || null);
      
      // Ažuriraj liked status ako je korisnik logovan
      if (user) {
        const { data: like } = await supabase
          .from("likes")
          .select("id")
          .eq("post_id", post.id)
          .eq("user_id", user.id)
          .maybeSingle();
        setLiked(!!like);
      }
    };
    
    void checkAuth();
  }, [post.id]);

  // Trenutni korisnik (prioritet: prop > state)
  const currentUser = currentUserId || authUserId;

  // Da li prati ovog korisnika
  const isFollowing = isCurrentUserFollowing || false;

  const handleLike = async (e: React.MouseEvent<HTMLButtonElement>): Promise<void> => {
    e.preventDefault();
    e.stopPropagation();

    // Ako nije logovan, redirect na login
    if (!isAuthenticated) {
      router.push("/login");
      return;
    }

    setLoading(true);
    const supabase = createClient();

    const {
      data: { user },
    } = await supabase.auth.getUser();

    if (!user) {
      router.push("/login");
      return;
    }

    try {
      if (liked) {
        await supabase
          .from("likes")
          .delete()
          .eq("post_id", post.id)
          .eq("user_id", user.id);
        setLiked(false);
        setLikesCount((prev) => prev - 1);
      } else {
        await supabase
          .from("likes")
          .insert({ 
            post_id: post.id, 
            user_id: user.id 
          });
        setLiked(true);
        setLikesCount((prev) => prev + 1);
      }
    } catch (error) {
      console.error("Error toggling like:", error);
    } finally {
      setLoading(false);
      // Refresh feed da bi se ažurirali broječi
      router.refresh();
    }
  };

  const handleFollowStatusChange = (isFollowing: boolean) => {
    if (onFollowChange && post.user_id) {
      onFollowChange(post.user_id, isFollowing);
    }
  };

  const handleCardClick = (e: React.MouseEvent<HTMLDivElement>): void => {
    // Provjeri da li je klik na link ili dugme
    const target = e.target as HTMLElement;
    if (target.closest("a, button, [role='button']")) {
      return;
    }
    router.push(`/post/${post.id}`);
  };

  const handleCommentButtonClick = (e: React.MouseEvent<HTMLButtonElement>): void => {
    e.preventDefault();
    e.stopPropagation();
    
    // Ako nije logovan, redirect na login
    if (!isAuthenticated) {
      router.push("/login");
      return;
    }
    
    router.push(`/post/${post.id}`);
  };

  const handleProfileLinkClick = (e: React.MouseEvent<HTMLAnchorElement>): void => {
    e.stopPropagation();
  };

  const getInitials = (name: string): string => {
    return name
      .split(" ")
      .map((part) => part[0])
      .join("")
      .toUpperCase()
      .slice(0, 2);
  };

  return (
    <div 
      onClick={handleCardClick}
      className="block transition-colors hover:bg-muted/50 cursor-pointer rounded-lg relative"
      role="button"
      tabIndex={0}
      onKeyDown={(e: React.KeyboardEvent<HTMLDivElement>) => {
        if (e.key === "Enter" || e.key === " ") {
          e.preventDefault();
          router.push(`/post/${post.id}`);
        }
      }}
    >
      <article className="p-4">
        <div className="flex gap-3">
          {/* Avatar link */}
          <div className="relative z-10">
            <Link 
              href={`/profile/${post.username}`} 
              onClick={handleProfileLinkClick}
              className="inline-block"
              aria-label={`View ${post.display_name}'s profile`}
            >
              <Avatar className="h-10 w-10">
                <AvatarImage 
                  src={post.avatar_url || undefined} 
                  alt={post.display_name} 
                />
                <AvatarFallback className="text-sm">
                  {getInitials(post.display_name)}
                </AvatarFallback>
              </Avatar>
            </Link>
          </div>

          <div className="flex-1 space-y-2">
            {/* User info and timestamp */}
            <div className="flex items-center justify-between">
              <div className="flex items-center gap-2 flex-wrap">
                <Link
                  href={`/profile/${post.username}`}
                  onClick={handleProfileLinkClick}
                  className="font-semibold hover:underline focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring"
                  aria-label={`View ${post.display_name}'s profile`}
                >
                  {post.display_name}
                </Link>
                
                <Link
                  href={`/profile/${post.username}`}
                  onClick={handleProfileLinkClick}
                  className="text-muted-foreground text-sm hover:underline focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring"
                  aria-label={`View @${post.username}'s profile`}
                >
                  @{post.username}
                </Link>
                
                <span className="text-muted-foreground text-sm">·</span>
                
                <span className="text-muted-foreground text-sm">
                  {formatDistanceToNow(new Date(post.created_at), { addSuffix: true })}
                </span>
              </div>

              {/* Follow button (desno) */}
              {showFollowButton && isAuthenticated && currentUser && currentUser !== post.user_id && (
                <div className="relative z-20" onClick={(e) => e.stopPropagation()}>
                  <FollowButton
                    followerId={currentUser}
                    followingId={post.user_id}
                    isFollowing={isFollowing}
                    onFollowChange={handleFollowStatusChange}
                  />
                </div>
              )}
            </div>

            {/* Post content */}
            <p className="whitespace-pre-wrap text-pretty break-words">{post.content}</p>

            {/* Actions */}
            <div className="flex items-center gap-6">
              {/* Comment button */}
              <Button
                variant="ghost"
                size="sm"
                className="h-8 gap-2 px-2 text-muted-foreground hover:text-primary relative z-10"
                onClick={handleCommentButtonClick}
                type="button"
                aria-label={`${isAuthenticated ? `View comments (${post.comments_count})` : 'Sign in to comment'}`}
              >
                <MessageCircle className="h-4 w-4" />
                <span className="text-sm">{post.comments_count}</span>
                {!isAuthenticated && (
                  <span className="absolute -top-1 -right-1">
                    <LogIn className="h-3 w-3 text-muted-foreground" />
                  </span>
                )}
              </Button>

              {/* Like button */}
              <Button
                variant="ghost"
                size="sm"
                className={`h-8 gap-2 px-2 relative z-10 ${
                  liked ? "text-red-500 hover:text-red-600" : "text-muted-foreground hover:text-red-500"
                }`}
                onClick={handleLike}
                disabled={loading || (!isAuthenticated && !liked)}
                type="button"
                aria-label={`${isAuthenticated ? (liked ? "Unlike" : "Like") : "Sign in to like"} post (${likesCount} likes)`}
              >
                <Heart className={`h-4 w-4 ${liked ? "fill-current" : ""}`} />
                <span className="text-sm">{likesCount}</span>
                {!isAuthenticated && !liked && (
                  <span className="absolute -top-1 -right-1">
                    <LogIn className="h-3 w-3 text-muted-foreground" />
                  </span>
                )}
              </Button>

              {/* Indikator da treba login */}
              {!isAuthenticated && (
                <span className="text-xs text-muted-foreground">
                  Sign in to interact
                </span>
              )}
            </div>
          </div>
        </div>
      </article>
    </div>
  );
}