"use client";

import { useEffect, useState } from "react";
import { createClient } from "@/lib/supabase/client";
import { PostCard } from "@/components/post/post-card";
import { Button } from "@/components/ui/button";
import { Loader2, RefreshCw, Sparkles, Users, MessageSquare, Globe } from "lucide-react";
import Link from "next/link";

interface Post {
  id: string;
  content: string;
  created_at: string;
  user_id: string;
  username: string;
  display_name: string;
  avatar_url: string | null;
  likes_count: number;
  comments_count: number;
  user_has_liked: boolean;
}

export default function PublicFeed() {
  const [posts, setPosts] = useState<Post[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  async function fetchPosts() {
    const supabase = createClient();
    setLoading(true);
    setError(null);

    try {
      // Dohvati sve postove sa najnovijim na vrhu
      const { data: allPosts, error: fetchError } = await supabase
        .from("posts")
        .select(
          `
          *,
          profiles!posts_user_id_fkey(username, display_name, avatar_url)
        `
        )
        .order("created_at", { ascending: false }) // Noviji postovi na vrhu
        .limit(50);

      if (fetchError) {
        console.error("Error fetching posts:", fetchError);
        if (fetchError.message?.includes("Could not find the table")) {
          setError("database_not_setup");
        } else {
          setError(fetchError.message);
        }
        setLoading(false);
        return;
      }

      if (!allPosts || allPosts.length === 0) {
        setPosts([]);
        setLoading(false);
        return;
      }

      // Formatiraj postove za nelogovane korisnike
      const formattedPosts = await Promise.all(
        allPosts.map(async (post) => {
          // Broj lajkova
          const { count: likesCount } = await supabase
            .from("likes")
            .select("*", { count: "exact", head: true })
            .eq("post_id", post.id);

          // Broj komentara
          const { count: commentsCount } = await supabase
            .from("comments")
            .select("*", { count: "exact", head: true })
            .eq("post_id", post.id);

          return {
            id: post.id,
            content: post.content,
            created_at: post.created_at,
            user_id: post.user_id,
            username: post.profiles.username,
            display_name: post.profiles.display_name,
            avatar_url: post.profiles.avatar_url,
            likes_count: likesCount ?? 0,
            comments_count: commentsCount ?? 0,
            user_has_liked: false, // Nelogovani ne mogu lajkovati
          };
        })
      );

      setPosts(formattedPosts);
    } catch (err) {
      console.error("Error fetching posts:", err);
      setError(err instanceof Error ? err.message : "Unknown error");
    } finally {
      setLoading(false);
    }
  }

  useEffect(() => {
    fetchPosts();
  }, []);

  if (loading) {
    return (
      <div className="min-h-screen bg-gradient-to-b from-background to-muted/20">
        <header className="sticky top-0 z-50 w-full border-b bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60">
          <div className="container mx-auto flex h-16 items-center justify-between px-4 md:px-6">
            <div className="flex items-center gap-2">
              <Sparkles className="h-6 w-6 text-primary" />
              <span className="text-xl font-bold bg-gradient-to-r from-primary to-purple-600 bg-clip-text text-transparent">
                Nexus
              </span>
            </div>
            <div className="flex gap-3">
              <Link href="/login">
                <Button variant="ghost" size="sm">Sign In</Button>
              </Link>
              <Link href="/register">
                <Button size="sm">Join Free</Button>
              </Link>
            </div>
          </div>
        </header>
        <div className="flex h-[calc(100vh-4rem)] items-center justify-center">
          <div className="text-center">
            <Loader2 className="h-8 w-8 animate-spin mx-auto mb-4" />
            <p className="text-muted-foreground">Loading the Nexus...</p>
          </div>
        </div>
      </div>
    );
  }

  if (error === "database_not_setup") {
    return (
      <div className="min-h-screen bg-gradient-to-b from-background to-muted/20">
        <header className="sticky top-0 z-50 w-full border-b bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60">
          <div className="container mx-auto flex h-16 items-center justify-between px-4">
            <div className="flex items-center gap-2">
              <Sparkles className="h-6 w-6 text-primary" />
              <span className="text-xl font-bold">Nexus</span>
            </div>
          </div>
        </header>
        <div className="container mx-auto max-w-4xl px-4 py-16">
          <div className="rounded-lg border bg-card p-8 text-center">
            <div className="mx-auto mb-6 flex h-12 w-12 items-center justify-center rounded-full bg-destructive/10">
              <Globe className="h-6 w-6 text-destructive" />
            </div>
            <h2 className="mb-2 text-2xl font-bold">Database Setup Required</h2>
            <p className="text-muted-foreground mb-6">
              Please run the SQL scripts in your Supabase project to create the necessary tables.
            </p>
          </div>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="min-h-screen bg-gradient-to-b from-background to-muted/20">
        <header className="sticky top-0 z-50 w-full border-b bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60">
          <div className="container mx-auto flex h-16 items-center justify-between px-4">
            <div className="flex items-center gap-2">
              <Sparkles className="h-6 w-6 text-primary" />
              <span className="text-xl font-bold">Nexus</span>
            </div>
            <div className="flex gap-3">
              <Link href="/login">
                <Button variant="ghost" size="sm">Sign In</Button>
              </Link>
              <Link href="/register">
                <Button size="sm">Join Free</Button>
              </Link>
            </div>
          </div>
        </header>
        <div className="container mx-auto max-w-4xl px-4 py-16">
          <div className="rounded-lg border bg-card p-8 text-center">
            <h2 className="mb-2 text-2xl font-bold">Connection Error</h2>
            <p className="text-muted-foreground mb-4">{error}</p>
            <Button onClick={fetchPosts} className="mt-4">
              <RefreshCw className="mr-2 h-4 w-4" />
              Try Again
            </Button>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gradient-to-b from-background to-muted/20">
      {/* Header */}
      <header className="sticky top-0 z-50 w-full border-b bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60">
        <div className="container mx-auto flex h-16 items-center justify-between px-4 md:px-6">
          <div className="flex items-center gap-2">
            <Sparkles className="h-6 w-6 text-primary" />
            <span className="text-xl font-bold bg-gradient-to-r from-primary to-purple-600 bg-clip-text text-transparent">
              Nexus
            </span>
            <span className="hidden text-sm text-muted-foreground md:inline">
              Connect with the world
            </span>
          </div>
          <div className="flex items-center gap-3">
            <Link href="/login">
              <Button variant="ghost" size="sm">Sign In</Button>
            </Link>
            <Link href="/register">
              <Button size="sm" className="bg-gradient-to-r from-primary to-purple-600">
                Join Free
              </Button>
            </Link>
          </div>
        </div>
      </header>

      {/* Hero Section */}
      <section className="border-b">
        <div className="container mx-auto max-w-4xl px-4 py-12 md:py-16">
          <div className="text-center">
            <h1 className="mb-4 text-4xl font-bold tracking-tight md:text-5xl">
              Welcome to <span className="bg-gradient-to-r from-primary to-purple-600 bg-clip-text text-transparent">Nexus</span>
            </h1>
            <p className="mx-auto mb-8 max-w-2xl text-lg text-muted-foreground">
              A modern social network where meaningful conversations happen. 
              Join thousands of people sharing ideas, stories, and connections.
            </p>
            <div className="flex flex-col gap-3 sm:flex-row sm:justify-center">
              <Link href="/register" className="w-full sm:w-auto">
                <Button size="lg" className="w-full bg-gradient-to-r from-primary to-purple-600">
                  <Sparkles className="mr-2 h-4 w-4" />
                  Create Account
                </Button>
              </Link>
              <Link href="/login" className="w-full sm:w-auto">
                <Button size="lg" variant="outline" className="w-full">
                  Sign In
                </Button>
              </Link>
            </div>
          </div>

          {/* Features */}
          <div className="mt-12 grid gap-6 sm:grid-cols-3">
            <div className="rounded-lg border bg-card p-6 text-center">
              <Users className="mx-auto mb-4 h-8 w-8 text-primary" />
              <h3 className="mb-2 font-semibold">Global Community</h3>
              <p className="text-sm text-muted-foreground">
                Connect with people from around the world
              </p>
            </div>
            <div className="rounded-lg border bg-card p-6 text-center">
              <MessageSquare className="mx-auto mb-4 h-8 w-8 text-primary" />
              <h3 className="mb-2 font-semibold">Real Conversations</h3>
              <p className="text-sm text-muted-foreground">
                Share ideas and engage in meaningful discussions
              </p>
            </div>
            <div className="rounded-lg border bg-card p-6 text-center">
              <Globe className="mx-auto mb-4 h-8 w-8 text-primary" />
              <h3 className="mb-2 font-semibold">Free Forever</h3>
              <p className="text-sm text-muted-foreground">
                No ads, no subscription fees, just genuine connections
              </p>
            </div>
          </div>
        </div>
      </section>

      {/* Feed Section */}
      <section className="container mx-auto max-w-2xl px-4 py-8">
        <div className="mb-8">
          <div className="flex items-center justify-between">
            <h2 className="text-2xl font-bold">Latest from Nexus</h2>
            <div className="flex gap-2">
              <Button variant="outline" size="sm" onClick={fetchPosts}>
                <RefreshCw className="mr-2 h-3 w-3" />
                Refresh
              </Button>
              <Link href="/register">
                <Button size="sm">Join to Post</Button>
              </Link>
            </div>
          </div>
          <p className="mt-2 text-sm text-muted-foreground">
            See what people are talking about right now. Sign in to join the conversation.
          </p>
        </div>

        {/* Posts */}
        {posts.length === 0 ? (
          <div className="rounded-lg border bg-card p-12 text-center">
            <MessageSquare className="mx-auto mb-4 h-12 w-12 text-muted-foreground/50" />
            <h3 className="mb-2 text-xl font-semibold">Quiet around here</h3>
            <p className="mb-4 text-muted-foreground">
              Be the first to break the silence and start a conversation
            </p>
            <Link href="/register">
              <Button>Create First Post</Button>
            </Link>
          </div>
        ) : (
          <>
            <div className="space-y-6">
              {posts.map((post) => (
                <PostCard key={post.id} post={post} />
              ))}
            </div>
            <div className="mt-8 text-center">
              <p className="text-sm text-muted-foreground">
                Want to see more?{" "}
                <Link href="/register" className="font-medium text-primary hover:underline">
                  Join Nexus
                </Link>{" "}
                to explore the full community.
              </p>
            </div>
          </>
        )}
      </section>

      {/* Footer CTA */}
      <div className="border-t bg-gradient-to-r from-primary/5 to-purple-600/5">
        <div className="container mx-auto max-w-4xl px-4 py-12">
          <div className="rounded-xl border bg-card p-8 text-center">
            <h2 className="mb-4 text-2xl font-bold">Ready to join the conversation?</h2>
            <p className="mb-6 text-muted-foreground">
              Create your account in seconds and start connecting with amazing people.
            </p>
            <div className="flex flex-col gap-3 sm:flex-row sm:justify-center">
              <Link href="/register">
                <Button size="lg" className="bg-gradient-to-r from-primary to-purple-600">
                  <Sparkles className="mr-2 h-4 w-4" />
                  Create Free Account
                </Button>
              </Link>
              <Link href="/login">
                <Button size="lg" variant="outline">
                  Already have an account? Sign In
                </Button>
              </Link>
            </div>
          </div>
        </div>
      </div>

      {/* Footer */}
      <footer className="border-t">
        <div className="container mx-auto max-w-4xl px-4 py-8">
          <div className="flex flex-col items-center justify-between gap-4 md:flex-row">
            <div className="flex items-center gap-2">
              <Sparkles className="h-5 w-5 text-primary" />
              <span className="font-bold">Nexus</span>
              <span className="text-sm text-muted-foreground">© {new Date().getFullYear()}</span>
            </div>
            <div className="flex gap-6">
              <Link href="/about" className="text-sm text-muted-foreground hover:text-foreground">
                About
              </Link>
              <Link href="/privacy" className="text-sm text-muted-foreground hover:text-foreground">
                Privacy
              </Link>
              <Link href="/terms" className="text-sm text-muted-foreground hover:text-foreground">
                Terms
              </Link>
              <Link href="/contact" className="text-sm text-muted-foreground hover:text-foreground">
                Contact
              </Link>
            </div>
          </div>
        </div>
      </footer>
    </div>
  );
}